import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AddBookingModuleComponent } from './add-booking-module.component';

const routes: Routes = [{ path: '', component: AddBookingModuleComponent }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddBookingModuleRoutingModule { }



