import { UserLogin } from 'core/models/userloginresponse';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Constants } from 'core/constants';
import { HttpUserUtilsService } from 'core/http_wrapper/http-user-utils.service';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { ClassDetail } from 'core/models/classListResponse';
import { LocalStorageService } from 'core/service/local-storage.service';
import { Router } from '@angular/router';
import { forEach } from '@angular/router/src/utils/collection';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { CustomData } from 'core/autocomplete/custom_data';

@Component({
  selector: 'app-add-booking-module',
  templateUrl: './add-booking-module.component.html',
  styleUrls: ['./add-booking-module.component.css']
})
export class AddBookingModuleComponent implements OnInit,HttpSuccesFailureResponse {
  upcommingClaasesDetail:ClassDetail[] = [];
  currentPage: number = 0;
  addBookingForm: FormGroup;
  addBookingFormGuest:FormGroup;
  userData:UserLogin;
  genderAccUpcommingClaasesDetail:ClassDetail[] = [];
  pkgTypeswiseClassesData:ClassDetail[] = [];
  userGender:any;
  allUserPackages:any=[];
  selectedPackageType:any;
  isExistingCustomerBooking='YES';
  gymId;
  isMasterGym:boolean=false;
  selectedSession:any;
  public customData: CustomData;
  public userSearch:any;

  constructor(private user_http: HttpUserUtilsService, private http_userauth: HttpUserAuthUtilsService,public formBuilder: FormBuilder,private router: Router) {
    this.customData = new CustomData(this.http_userauth);

   }

   getClassesAccordingToUserGender(){
     if(!this.userGender)
     {
     return;
     }
    this.addBookingForm.patchValue({session:''});
    this.addBookingForm.patchValue({sessionId:''});
    this.genderAccUpcommingClaasesDetail=[];
    
     
    this.upcommingClaasesDetail.forEach(element => {
        if(element.classType=="MIXED" || element.classType==this.userGender){
           this.genderAccUpcommingClaasesDetail.push(element);
        }
       
      });
      console.log(this.genderAccUpcommingClaasesDetail)
     // this.upcommingClaasesDetail=this.genderAccUpcommingClaasesDetail;
     
   }
   onUserSelected(event:any){

    this.http_userauth.getUserProfileByEmail(event.originalObject.email,this,true)
    }
   selectPackageToBookClass(event){
    this.allUserPackages.forEach(element => {
        console.log(element);
    });
    if (event.target.value) {
      this.addBookingForm.patchValue({sessionId:event.target.value});
      this.getSelectedPkgData(event.target.value);
    } 
  }
  isExistingCustomerAddBooking(status){
this.isExistingCustomerBooking=status;
  }

  getSelectedPkgData(packageType:any){
    
    if(!packageType){
      return
    }
    this.pkgTypeswiseClassesData=[];
    this.selectedPackageType = packageType;
    console.log(packageType)
  
    if(packageType=="ARIALPACK"){
      this.genderAccUpcommingClaasesDetail.forEach(element=>{
        if(element.arialClass==true)  this.pkgTypeswiseClassesData.push(element);
      });
    }
    else{
      this.genderAccUpcommingClaasesDetail.forEach(element=>{
        if(!element.arialClass)  this.pkgTypeswiseClassesData.push(element);
      });
    }
  
  }

  onSuccess(type: any, responsedata: any) {
    if(type==Constants.TrainerUpcomingClassesApi){
      let upcommingClassTemp:ClassDetail[] = responsedata.data;
      this.upcommingClaasesDetail=[];
       upcommingClassTemp.forEach(element => {
        if(element.availableSeat>0){
          this.upcommingClaasesDetail.push(element);
        }
      });


      if(!this.upcommingClaasesDetail||this.upcommingClaasesDetail.length==0){
        if(this.isExistingCustomerBooking=='YES'){
          this.addBookingForm.patchValue({errorMessage:'No session available for selected date please choose diffrent date.'});
          this.addBookingForm.updateValueAndValidity(); 
        }else{
          this.addBookingFormGuest.patchValue({errorMessage:'No session available for selected date please choose diffrent date.'});
          this.addBookingFormGuest.updateValueAndValidity(); 
        }
       
      }
      if(this.isExistingCustomerBooking=='YES'){
        this.getClassesAccordingToUserGender();
        this.getSelectedPkgData(this.selectedPackageType)
      }else{
        
       this.pkgTypeswiseClassesData = this.upcommingClaasesDetail.filter((element) => {
        return element.priceModel != "AUTOJOINWITHPACKAGE";
      }); 
      }

   
    

    }
    
    if(type == Constants.findUserByEmailApi){
      this.addBookingForm.patchValue({errorMessage:''});
      this.addBookingForm.updateValueAndValidity();
       this.userData = responsedata;

       this.userGender=this.userData.data.gender;
       this.allUserPackages=this.userData.data.userPackageDetailDtoList;
  console.log(this.userData)
      this.addBookingForm.patchValue({name:this.userData.data.firstName});
      try{
        this.allUserPackages=this.userData.data.userPackageDetailDtoList.filter((element) => {
          if(element.gymId==LocalStorageService.getUserId()){
            return true;
          }
     
        });
        this.userData.data.userPackageDetailDtoList=this.userData.data.userPackageDetailDtoList.filter((element) => {
          if(element.gymId==LocalStorageService.getUserId()){
            return true;
          }
     
        });
      }
      catch(e){

      }
      if(this.userData.data.userPackageDetailDtoList&&this.userData.data.userPackageDetailDtoList.length>0){
       
       let packageDetail =  this.userData.data.userPackageDetailDtoList[0].balanceClass+" Of "+this.userData.data.userPackageDetailDtoList[0].totalNumberOfClass+" remaining";
        this.addBookingForm.patchValue({package:packageDetail});
        this.addBookingForm.patchValue({packageBalance:this.userData.data.userPackageDetailDtoList[0].balanceClass});

      }else{
        this.addBookingForm.patchValue({package:"NA"});
        this.addBookingForm.patchValue({errorMessage:"User has no balance. Add package before proceeding with adding a booking"});

      }
      this.upcommingClaasesDetail=[];
      this.addBookingForm.updateValueAndValidity();
     


    }
    if(type==Constants.JoinClassApi){
      alert("Booking is successfully added");
     this.router.navigate(['/gym/bookings']); 
  // setTimeout(() => {
      //   this.router.navigate(['/gym/bookings']); 
      // }, 500);
      
    }
  }
  onFailure(type: any, response: any) {
    if(type==Constants.findUserByEmailApi){
      this.addBookingForm.patchValue({errorMessage:'User email does not exist.'});
      this.addBookingForm.updateValueAndValidity(); 
    }
    if(type==Constants.TrainerUpcomingClassesApi){
      
    }  
    if(type==Constants.JoinClassApi){
      if (response.statusCode == Constants.gymVisitLimitExeeded ) {
        this.addBookingForm.patchValue({errorMessage:'Customer have exceeded the number of visits.'});
    this.addBookingForm.updateValueAndValidity();
    
      }else if (response.statusCode == Constants.paymentRequiredError) { 
        if(this.userData.data.userPackageDetailDtoList&&this.userData.data.userPackageDetailDtoList.length>0){
          
          this.addBookingForm.patchValue({errorMessage:'Customers package expiring before the class.Please add extra days to this account in order to confirm this booking. "'});
    this.addBookingForm.updateValueAndValidity(); 
        }else{
          if(this.isExistingCustomerBooking=='YES'){
            this.addBookingForm.patchValue({errorMessage:'This user does not have an existing plan. Please add a plan to this account in order to confirm this booking.'});
            this.addBookingForm.updateValueAndValidity(); 
          }else{
            this.addBookingFormGuest.patchValue({errorMessage:"You have already booked for this class"});
            this.addBookingFormGuest.updateValueAndValidity();
          }
          

        }
    }
    else if (JSON.parse(response._body).statusCode === '4XX'){
      if(this.isExistingCustomerBooking=='YES'){
        this.addBookingForm.patchValue({errorMessage:JSON.parse(response._body).errorMessage});
        this.addBookingForm.updateValueAndValidity();
      }else{
        this.addBookingFormGuest.patchValue({errorMessage:JSON.parse(response._body).errorMessage});
        this.addBookingFormGuest.updateValueAndValidity();
      }

  
      return;

    }
    else if (JSON.parse(response._body).statusCode === Constants.paymentRequiredError){
      if(this.userData.data.userPackageDetailDtoList&&this.userData.data.userPackageDetailDtoList.length>0){
        this.addBookingForm.patchValue({errorMessage:'This session cannot be booked by selected package.'});
  this.addBookingForm.updateValueAndValidity(); 
      }else{
        this.addBookingForm.patchValue({errorMessage:'This user does not have an existing plan. Please add a plan to this account in order to confirm this booking.'});
        this.addBookingForm.updateValueAndValidity(); 

      }
    }
    else if (JSON.parse(response._body).statusCode === Constants.gymVisitLimitExeeded){
      this.addBookingForm.patchValue({errorMessage:'Customer have exceeded the number of visits.'});
    this.addBookingForm.updateValueAndValidity();
    }

  
    }
  
  }

  ngOnInit(): void {
    this.gymId = LocalStorageService.getUserId();
    this.isMasterGym = LocalStorageService.isMasterGym();
    this.addBookingForm = this.formBuilder.group({
      email: ['', [Validators.required]],
      name: ['', [Validators.maxLength(600), Validators.required]],
      package: ['', [Validators.required]],
      classDate:['',[Validators.required]],
      packageBalance:[''],
      session:[''],
      sessionId:['',[Validators.required]],
      errorMessage:[''],
      packageId:[''],

     });
     this.addBookingFormGuest = this.formBuilder.group({
      name: ['', [Validators.maxLength(600), Validators.required]],
      classDate:['',[Validators.required]],
      session:[''],
      sessionId:['',[Validators.required]],
      errorMessage:['']
    });

     


//  this.user_http.getgymUpcommingClasses(LocalStorageService.getUserId(), this.currentPage, this, true)
  //this.user_http.getgymUpcommingClassesUnlimited(LocalStorageService.getUserId(), this.currentPage, this, true)

  }

  findUser(){
    if(this.addBookingForm.value.email){
      this.http_userauth.getUserProfileByEmail(this.addBookingForm.value.email,this,true);
    }
  }
  findSession(){
    if(this.isExistingCustomerBooking=='YES'){
      if(this.addBookingForm.value.classDate){
        this.addBookingForm.patchValue({errorMessage:''});
        this.addBookingForm.updateValueAndValidity(); 
        this.pkgTypeswiseClassesData=[];
  
        this.user_http.getgymUpcommingClassesByDate(this.addBookingForm.value.classDate,this.gymId, this, true)
  
      }
    }else{
      if(this.addBookingFormGuest.value.classDate){
        this.addBookingFormGuest.patchValue({errorMessage:''});
        this.addBookingFormGuest.updateValueAndValidity(); 
        this.pkgTypeswiseClassesData=[];
  
        this.user_http.getgymUpcommingClassesByDate(this.addBookingFormGuest.value.classDate,this.gymId, this, true)
  
      }
    }
    
  }


  selectSession(event){
    if(this.isExistingCustomerBooking=='YES'){
      if (event.target.value) {
        this.addBookingForm.patchValue({sessionId:event.target.value});
      } 
    }else{
      if (event.target.value) {
        this.addBookingFormGuest.patchValue({sessionId:event.target.value});
      } 
    }
    this.upcommingClaasesDetail.forEach(element => {
      if(element.classSessionId==event.target.value){
        this.selectedSession = element;


      }
    });



    
  }
  addBooking(){

    if(this.isExistingCustomerBooking=='YES'){
      this.addBookingForm.patchValue({errorMessage:''});
      this.addBookingForm.updateValueAndValidity();
      if(this.addBookingForm.valid){
        this.http_userauth.joinClassRequest(this.userData.data.id,this.addBookingForm.value.sessionId,LocalStorageService.getUser().data.id,this,true)
      }else{
      this.markFormGroupTouched(this.addBookingForm);
      }
    }else{
      this.addBookingFormGuest.patchValue({errorMessage:''});
      this.addBookingFormGuest.updateValueAndValidity();
      if(this.addBookingFormGuest.valid){
        this.http_userauth.joinClassRequestGuest(this.addBookingFormGuest.value.name,this.addBookingFormGuest.value.sessionId,LocalStorageService.getUser().data.id,this,true)
      }else{
      this.markFormGroupTouched(this.addBookingForm);
      }
    }
    
  }
  private markFormGroupTouched(formGroup: FormGroup) {
    try {
      (<any>Object).values(formGroup.controls).forEach(control => {
        control.markAsTouched();
        if (control.controls) {
          control.controls.forEach(c => this.markFormGroupTouched(c));
        }
      });
    } catch (ex) {
      // Code to handle exception
    }


  }
}



