import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { AddBookingModuleRoutingModule } from './add-booking-module-routing.module';
import { AddBookingModuleComponent } from './add-booking-module.component';
import { ReactiveFormsModule } from '@angular/forms';
import { CoreModule } from 'core/core.module';


@NgModule({
  declarations: [AddBookingModuleComponent],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    CoreModule,
    AddBookingModuleRoutingModule
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ]

})
export class AddBookingModuleModule { }



